import React, { Component , Suspense } from 'react'
import imgDev from './undraw_web_developer_p3e5.svg';
import BackEnd from './undraw_code_review.svg';
import Extra from './undraw_dev_productivity_umsq.svg';
import Cloud from './undraw_cloud_hosting_aodd.svg';
import Networking from './undraw_connected_world.svg';
const Skill = React.lazy(() => import('./Skill'));

export default class Habilities extends Component {
    render() {
        return (
            <div className="uk-section uk-section-default uk-text-center">
                <div className="uk-container uk-container-xlarge">

                    <h2 className="uk-margin-bottom">HABILIDADES</h2>
                    <div className="uk-child-width-1-3@m uk-grid-medium uk-grid-match uk-flex uk-flex-center" data-uk-grid>
                        <Suspense fallback={<span data-uk-spinner="ratio: 8"></span>}>
                            <Skill
                                image={imgDev}
                                title="Frontend" 
                                width = "325"
                                description="Mi experiencia se basa en desarrollo utilizando javascript, experiencia en técnicas HTML5, CSS3 y diseño responsivo. He empleado frameworks como UiKit, Materialize, Bootstrap y React JS.">
                            </Skill>
                            <Skill
                                image={BackEnd}
                                width = "350"
                                title="Backend" 
                                description="Mi principal fuerte es el uso de C# seguido de Java para el entorno de escritorio. He utilizado nodejs, python, php y C# como lenguaje para backend en proyectos web. Tengo conocimiento sobre consumo y creación de APIs.">
                            </Skill>

                            <Skill
                                image={Cloud}
                                width = "300"
                                title="Cómputo en la nube" 
                                description="Mi experiencia sobre la nube me da un panorama bastante amplio donde se abre infinidad de opciones para crear nuevos desarrollos. Realicé el curso de fundamentos de la nube de AWS donde pude explorar servicios de IaaS y PaaS, también he trabajado con Heroku, Netlify, FireBase y en menor medida, DigitalOcean.">
                            </Skill>
                            
                            <Skill
                                image={Networking}
                                width = "300"
                                title="Redes" 
                                description="Tengo dominio sobre el manejo de redes. Tomé cuatro cursos de CCNA donde obtuve conocimientos acerca de ACL, enrutamiento, VLSM, configuración de routers, switches de Cisco, entre otros aspectos técnicos.">
                            </Skill>

                            <Skill
                                image={Extra}
                                width = "250"
                                title="Soporte técnico" 
                                description="En este ámbito, es esencial conocer sobre las fallas que puede presentar un equipo. He realizado varios ensambles de computadoras de escritorio y reparado leves fallas en unas cuantas laptops. También puedo realizar instalaciones de sistemas operativos">
                            </Skill>
                        </Suspense>
                    </div>
                </div>
                <span id="Knowledge"></span>
            </div>
        )
    }
}
